<template>
    <v-container class="pa-0" style="position: relative;">
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{languageOfAppText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card style="margin-top: 68px;">
            <v-list two-line active-class="primary--text" style="padding: 0px!important">
                <v-list-item-group v-model="selectedLanguage" color="primary">
                    <v-list-item @click="spanishSelected">
                        <v-list-item-content>
                            <v-list-item-title>
                                <img src="img/flags/squared_es.svg" :aspect-ratio="1"
                                     style="height: 20px; vertical-align: bottom">
                                &nbsp;&nbsp;{{languageESText}}
                            </v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>

                    <v-divider></v-divider>

                    <v-list-item @click="englishSelected">
                        <v-list-item-content>
                            <v-list-item-title>
                                <img src="img/flags/squared_en.svg" :aspect-ratio="1"
                                     style="height: 20px; vertical-align: bottom">
                                &nbsp;&nbsp;{{languageENText}}
                            </v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>

</template>

<script>

    // Página de actualización del idioma de la aplicación asociado a la cuenta del usuario autenticado
    // @group Configuración
    export default {
        name: "SettingsLanguage",
        data: () => ({
            snackbar: false,
            snackbarText: null,
            selectedLanguage: null

        }),
        methods: {
            // @vuese
            // Establece el Español como idioma de la aplicación para la cuenta del usuario autenticado
            spanishSelected: function () {
                this.$i18n.locale = 'es';
                this.$forceUpdate();
                this.saveAccountLanguage('es');
            },
            // @vuese
            // Establece el Inglés como idioma de la aplicación para la cuenta del usuario autenticado
            englishSelected: function () {
                this.$i18n.locale = 'en';
                this.$forceUpdate();
                this.saveAccountLanguage('en');
            },
            // @vuese
            // Guarda el idioma pasado como parámetro como idioma de la aplicación para la cuenta del usuario autenticado y traduce la misma a este idioma
            // @arg El parámetro es el identificador del idioma seleccionado por el usuario
            saveAccountLanguage: function (lang) {
                let self = this;
                this.$store.dispatch('user/setLanguage', lang)
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            }
        },
        computed: {
            // @vuese
            // Devuelve el texto de idioma de la app en formato i18n
            languageOfAppText: function () {
                return this.$i18n.t('messages.TEXT_LANGUAGE_OF_APP');
            },
            // @vuese
            // Devuelve el texto 'Español' de la app en formato i18n
            languageESText: function () {
                return this.$i18n.t('messages.TEXT_LANGUAGE_ES');
            },
            // @vuese
            // Devuelve el texto 'Inglés' de la app en formato i18n
            languageENText: function () {
                return this.$i18n.t('messages.TEXT_LANGUAGE_EN');
            },
            // @vuese
            // Devuelve el texto 'Cerrar' de la app en formato i18n
            closeText: function () {
                return this.$i18n.t('messages.TEXT_CLOSE');
            }
        },
        created() {
            let locale = this.$i18n.locale;
            if (locale === 'es') {
                this.selectedLanguage = 0;
            } else if (locale === 'en') {
                this.selectedLanguage = 1;
            }
        }
    }
</script>

<style scoped>

</style>
